section {
    padding: 50px 0;

    .inner {
        width: 1200px;
        margin: 0 auto;

        h2 {
            font-size: 23px;
            font-weight: 700;
            text-align: center;
        }

        table {
            margin: 30px 0 0 0;
            border-top: 2px solid #000;
            width: 100%;
            border-spacing: 0;
            border-collapse: collapse;

            .non {
                height: 100px;
            }

            .title {
                width: 800px;
            }

            tr {
                border-bottom: 1px solid #ddd;

                th {
                    padding: 15px 0;
                    font-size: 17px;
                    font-weight: 500;
                    border-bottom: 1px solid #000;
                    background: #f7f7f7;
                }

                td {
                    padding: 10px 0;
                    font-size: 15px;
                    line-height: 1.4;
                    text-align: center;
                }

                .num {
                    padding: 0 10px;
                }

                .tit {
                    width: 800px;
                    padding: 0 0 0 30px;
                    text-align: left;

                    a {
                        display: block;
                    }
                }
            }
        }
    }
}



.btn {
    padding: 30px 0 0 0;
    text-align: right;

    button {
        margin: 0 0 0 auto;
        border: 1px solid #ced4da;
        padding: 7px 20px;
        background: #f8f9fa;
        font-size: 15px;
        border-radius: 3px;
        cursor: pointer;
    }
}

.Write{
    h3 {
        text-align: center;
        font-size: 17px;
    }
    
    input,
    textarea {
        width: 100%;

    }
    :focus {
        border: 2px solid #333;
    }
    
    textarea {
        min-height: 150px;
    }
}


.BDitem {

    .item {
        .top{
            display: flex;
            justify-content: space-between;
            background: #f7f7f7;
            border-top: 1px solid #ddd;
            border-bottom: 1px solid #ddd;
            padding: 20px;

            h3 {
                font-size: 17px;
                font-weight: 500;
            }

            span {
                color: #707070;
                font-size: 15px;
            }
        }
        
        p {
            padding: 20px;
            font-size: 15px;
            font-weight: 500;
            border-bottom: 1px solid #ddd;
        }
    }
}

.EditItem {
    display: flex;
    flex-direction: column;

    .tit {
        background: #f7f7f7;
        border: none;
        border-top: 1px solid #ddd;
        border-bottom: 1px solid #ddd;
        padding: 20px;
    }

    .content {
        padding: 20px;
        font-size: 15px;
        font-weight: 500;
        border-bottom: 1px solid #ddd;
    }
}

.itembtn {
    width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: end;
    gap: 10px;
    padding: 50px 0 0 0;
    text-align: right;

    button {
        border: 1px solid #ced4da;
        padding: 7px 20px;
        background: #f8f9fa;
        font-size: 15px;
        border-radius: 3px;
        cursor: pointer;
    }
}